import axios from "axios";
import { db } from "@/main";

const state = {
  post: "",
  posts: [],
  postsAll: []
};

const getters = {
  getPost: state => state.post,
  getPosts: state => state.posts,
  getPostsAll: state => state.postsAll
};

const actions = {
  async addPost({ commit }, data) {
    const info = {
      content: data.content,
      image: data.image,
      video: data.video
    };

    const response = await axios.post(`${db}/pages/${data.page}/posts`, info, {
      headers: {
        "Content-Type": "application/json"
      }
    });

    commit("newPost", response.data);
  },
  async fetchPost({ commit }, id) {
    const response = await axios.get(`${db}/posts/${id}`);

    commit("setPost", response.data);
  },
  async fetchPosts({ commit }, id) {
    const response = await axios.get(`${db}/pages/${id}/posts`);

    commit("setPosts", response.data);
  },
  async fetchPostsAll({ commit }) {
    const response = await axios.get(`${db}/posts`);

    commit("setPostsAll", response.data);
  }
};

const mutations = {
  newPost: (state, post) => state.posts.unshift(post),
  setPost: (state, post) => (state.post = post),
  setPostsAll: (state, posts) => (state.postsAll = posts),
  setPosts: (state, posts) => (state.posts = posts)
};

export default {
  state,
  getters,
  actions,
  mutations
};
