import axios from "axios";
import { db } from "@/main";

const state = {
  page: "",
  myPages: [],
  pagesByCity: [],
  pagesByCountry: [],
  pagesByState: [],
  pages: []
};

const getters = {
  getMyPages: state => state.myPages,
  getPage: state => state.page,
  getPagesByCity: state => state.pagesByCity,
  getPagesByCountry: state => state.pagesByCountry,
  getPagesByState: state => state.pagesByState,
  getPages: state => state.pages
};

const actions = {
  async addPage({ commit }, data) {
    const info = {
      name: data.name,
      slug: data.slug,
      userId: data.userId,
      geoLat: data.geo.geoLat,
      geoLon: data.geo.geoLon,
      image: data.image,
      description: data.description,
      location: data.location,
      phone: data.phone,
      mobile: data.mobile,
      countryId: data.country,
      stateId: data.state,
      cityId: data.city
    };

    const response = await axios.post(`${db}/pages`, info, {
      headers: {
        "Content-Type": "application/json"
      }
    });

    commit("newPage", response.data);
  },
  async fetchPage({ commit }, id) {
    const response = await axios.get(`${db}/pages/${id}`);
    const data = {
      id: response.data._id,
      name: response.data.name,
      slug: response.data.slug,
      user: response.data.userId,
      geoLat: response.data.geo.lat,
      geoLon: response.data.geo.lon,
      image: response.data.image,
      description: response.data.description,
      location: response.data.location,
      phone: response.data.phone,
      mobile: response.data.mobile,
      countryId: response.data.countryId._id,
      countryName: response.data.countryId.name,
      countrySlug: response.data.countryId.slug,
      stateId: response.data.stateId._id,
      stateCode: response.data.stateId.code,
      stateName: response.data.stateId.name,
      cityId: response.data.cityId._id,
      cityCode: response.data.cityId.code,
      cityName: response.data.cityId.name,
      isActive: response.data.isActive,
      isLock: response.data.isLock,
      createdAt: response.data.createdAt
    };

    commit("setPage", data);
  },
  async fetchMyPages({ commit }, id) {
    const response = await axios.get(`${db}/users/${id}/pages`);

    commit("setMyPages", response.data);
  },
  async fetchPagesByCity({ commit }, id) {
    const response = await axios.get(`${db}/cities/${id}/pages`);

    commit("setPagesByCity", response.data);
  },
  async fetchPagesByCountry({ commit }, id) {
    const response = await axios.get(`${db}/countries/${id}/pages`);

    commit("setPagesByCountry", response.data);
  },
  async fetchPagesByState({ commit }, id) {
    const response = await axios.get(`${db}/states/${id}/pages`);

    commit("setPagesByState", response.data);
  },
  async fetchPages({ commit }) {
    const response = await axios.get(`${db}/pages`);

    commit("setPages", response.data);
  }
};

const mutations = {
  newPage: (state, page) => state.pages.unshift(page),
  setMyPages: (state, myPages) => (state.myPages = myPages),
  setPage: (state, page) => (state.page = page),
  setPagesByCity: (state, pagesByCity) => (state.pagesByCity = pagesByCity),
  setPagesByCountry: (state, pagesByCountry) =>
    (state.pagesByCountry = pagesByCountry),
  setPagesByState: (state, pagesByState) => (state.pagesByState = pagesByState),
  setPages: (state, pages) => (state.pages = pages)
};

export default {
  state,
  getters,
  actions,
  mutations
};
