import axios from "axios";
import { db } from "@/main";

const state = {
  admin: "",
  admins: []
};

const getters = {
  getAdmin: state => state.admin,
  getAdmins: state => state.admins
};

const actions = {
  async fetchAdmin({ commit }, id) {
    const response = await axios.get(`${db}/admins/${id}`);

    commit("setAdmin", response.data);
  },
  async fetchAdmins({ commit }) {
    const response = await axios.get(`${db}/admins`);

    commit("setAdmins", response.data);
  }
};

const mutations = {
  setAdmin: (state, admin) => (state.admin = admin),
  setAdmins: (state, admins) => (state.admins = admins)
};

export default {
  state,
  getters,
  actions,
  mutations
};
