import axios from "axios";
import { db } from "@/main";

const state = {
  comment: "",
  commentsByPost: []
};

const getters = {
  getCommentsByPost: state => state.commentsByPost
};

const actions = {
  async fetchCommentsByPost({ commit }, id) {
    const response = await axios.get(`${db}/posts/${id}/comments`);
    const data = {
      cId: response.data._id,
      cUser: response.data.userId,
      cPost: response.data.postId,
      cComment: response.data.comment,
      cCreatedAt: response.data.createdAt
    };

    commit("setCommentByPost", data);
  }
};

const mutations = {
  setCommentByPost: (state, posts) => (state.commentsByPost = posts)
};

export default {
  state,
  getters,
  actions,
  mutations
};
