import axios from "axios";
import { db } from "@/main";

const state = {
  departamento: "",
  departamentos: []
};

const getters = {
  getState: state => state.departamento,
  getStates: state => state.departamentos
};

const actions = {
  async fetchState({ commit }, id) {
    const response = await axios.get(`${db}/states/${id}`);

    commit("setState", response.data);
  },
  async fetchStates({ commit }, id) {
    const response = await axios.get(`${db}/countries/${id}/states`);

    commit("setStates", response.data);
  }
};

const mutations = {
  setState: (state, departamento) => (state.departamento = departamento),
  setStates: (state, departamentos) => (state.departamentos = departamentos)
};

export default {
  state,
  getters,
  actions,
  mutations
};
