import axios from "axios";
import { db } from "@/main";

const state = {
  superuser: "",
  superusers: []
};

const getters = {
  getSuperuser: state => state.superuser,
  getSuperusers: state => state.superusers
};

const actions = {
  async fetchSuperuser({ commit }, id) {
    const response = await axios.get(`${db}/superusers/${id}`);

    commit("setSuperuser", response.data);
  },
  async fetchSuperusers({ commit }) {
    const response = await axios.get(`${db}/superusers`);

    commit("setSuperusers", response.data);
  }
};

const mutations = {
  setSuperuser: (state, superuser) => (state.superuser = superuser),
  setSuperusers: (state, superusers) => (state.superusers = superusers)
};

export default {
  state,
  getters,
  actions,
  mutations
};
