import axios from "axios";
import { db } from "@/main";

const state = {
  city: "",
  cities: []
};

const getters = {
  getCities: state => state.cities,
  getCity: state => state.city
};

const actions = {
  async fetchCities({ commit }, id) {
    const response = await axios.get(`${db}/states/${id}/cities`);

    commit("setCities", response.data);
  },
  async fetchCity({ commit }, id) {
    const response = await axios.get(`${db}/cities/${id}`);

    commit("setCity", response.data);
  }
};

const mutations = {
  setCities: (state, cities) => (state.cities = cities),
  setCity: (state, city) => (state.city = city)
};

export default {
  state,
  getters,
  actions,
  mutations
};
